import { useState, useEffect } from 'react';
import { FaEdit, FaLock, FaUserAlt } from 'react-icons/fa';
import Sidebar from '../components/Sidebar';
import styles from './Perfil.module.css'; // Importando o CSS isolado

const Perfil: React.FC = () => {
  const [user, setUser] = useState<User | null>(null);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // Funções de atualização de perfil e senha
  const handleProfileUpdate = () => {
    // Função para atualizar o perfil
    setSuccess('Perfil atualizado com sucesso!');
  };

  const handlePasswordChange = () => {
    // Função para alterar a senha
    if (password === confirmPassword) {
      setSuccess('Senha alterada com sucesso!');
    } else {
      setError('As senhas não coincidem!');
    }
  };

  return (
    <div className={styles.container}>
      <Sidebar />
      <div className={styles.dashboardContent}>
        <h1 className={styles.title}>
          <FaUserAlt className={styles.icon} />
          Perfil de Usuário
        </h1>

        {error && <p className={styles.error}>{error}</p>}
        {success && <p className={styles.success}>{success}</p>}

        {/* Formulário de Perfil */}
        <div className={styles.formSection}>
          <h2>Dados do Perfil</h2>
          <div className={styles.inputGroup}>
            <label>Nome:</label>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </div>
          <div className={styles.inputGroup}>
            <label>Email:</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <button className={styles.button} onClick={handleProfileUpdate}>
            <FaEdit className={styles.icon} />
            Atualizar Perfil
          </button>
        </div>

        {/* Formulário de Alteração de Senha */}
        <div className={styles.formSection}>
          <h2>Alterar Senha</h2>
          <div className={styles.inputGroup}>
            <label>Nova Senha:</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>
          <div className={styles.inputGroup}>
            <label>Confirmar Nova Senha:</label>
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            />
          </div>
          <button className={styles.button} onClick={handlePasswordChange}>
            <FaLock className={styles.icon} />
            Alterar Senha
          </button>
        </div>
      </div>
    </div>
  );
};

export default Perfil;
